import ReactEcharts from "echarts-for-react";
import React from "react";
import { ColumnCenter } from "../styled";
import { HeaderWrap, HeaderItem, HeaderText, Line, SubTitle } from "./styled";
import { IMapInfo } from "./action";
import { MapTable } from "./MapTable";

export const Map: React.FC<{ mapData: IMapInfo }> = ({ mapData }) => {
  const mapOption1 = {
    tooltip: {
      trigger: "axis",
      axisPointer: {
        // 坐标轴指示器，坐标轴触发有效
        type: "line", // 默认为直线，可选为：'line' | 'shadow'
      },
    },
    xAxis: {
      type: "category",
      data: mapData.map_score_trend_date,
    },
    yAxis: {
      type: "value",
    },
    series: [
      {
        data: mapData.map_score_trend_value,
        type: "line",
      },
    ],
  };

  const mapHeaderArr = [
    {
      label: "RIT指标分值",
      value: mapData.rit_score || "-",
    },
    {
      label: "测试时长(minutes)",
      value: mapData.test_duration || "-",
    },
    {
      label: "测试日期",
      value: mapData.test_date || "-",
    },
  ];

  return (
    <ColumnCenter>
      <HeaderWrap style={{ marginTop: 10 }}>
        {mapHeaderArr.map((v, i, arr) => (
          <HeaderItem key={i} style={i % 2 === 0 ? {} : { textAlign: "right" }}>
            <span>{v.label}</span>
            <HeaderText style={i === 1 ? { fontWeight: "bolder" } : {}}>
              {v.value}
            </HeaderText>
          </HeaderItem>
        ))}
      </HeaderWrap>
      <Line></Line>
      <div>
        <SubTitle
          style={{
            marginTop: 0,
            color: "#333",
            marginBottom: 0,
            textAlign: "left",
          }}
        >
          RIT变化趋势
        </SubTitle>
        <ReactEcharts
          option={
            mapOption1 as echarts.EChartOption<echarts.EChartOption.Series>
          }
          theme="Imooc"
          style={{ height: "350px", width: "1400px" }}
        />
      </div>
      <Line></Line>
      <MapTable
        achievement_above_mean={mapData.achievement_above_mean}
        flesch_kincaid_grade_level={mapData.flesch_kincaid_grade_level}
        growth_goals_date={mapData.growth_goals_date}
        lexile_score={mapData.lexile_score}
        sub_domains_info={mapData.sub_domains_info}
      />

      <Line></Line>
    </ColumnCenter>
  );
};
